.info-detail {
  .table {
    .rc-table-cell{
      .liquidate {
        border : 1px solid #FFFFFF;
        padding: 4px 16px;
        border-radius: 16px;
        color : #ffffff;
      }
      .short,.removeLiquidity{
        border : 1px solid #CE5E5E;
        color: #CE5E5E;
        padding: 4px 16px;
        border-radius: 16px;
      }
      .long,.addliquidity{
        border : 1px solid #59AE99;
        color: #59AE99;
        padding: 4px 16px;
        border-radius: 16px;
      }
    }
  }
}
.info-detail{
  .tip-cursor{
    width: 100px;
    height: 40px;
    border:1px solid #203B60;
    background-color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 999;
  }
  .token-list{
    position: absolute;
    right: 46px;
    top: -300px;
    li{
      list-style: none;
      font-size: 20px;
      display: flex;
      width: 180px;
      justify-content: space-between;
      margin-top: 8px;
      span{
        display: flex;
        img{
          margin: 0 8px;
        }
      }
      .token-name{
        width: 95px;
      }
    }
  }
}

@media only screen and (max-width: 760px) {
  
  .info-main {
    .list-by-chain {
      .table {
        .rc-table-cell:first-child{
          padding-left: 12px;
        }
        .rc-table-cell {
          min-width: 150px;
          text-align: left;
        }
      }
    }
  }
}